/* global
    Carousel
 */
'use strict';

(function() {
  /**
   * Render the carousel.
   */
  window.renderCarousel = () => {
    new Carousel({
      panels: document.getElementsByClassName('carousel-panel'),
      leftArrow: document.getElementsByClassName('carousel-left')[0],
      rightArrow: document.getElementsByClassName('carousel-right')[0],
    });

    // setInterval(() => {
    //   carousel.next();
    // }, 5000);
  };
})();
